// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import { Aside
  , Breadcrumb
  , BreadcrumbItem
  , Button
  , Checkbox
  , Col
  , Container
  , Form
  , FormItem
  , Header
  , Main
  , Menu
  , MenuItem
  , MenuItemGroup
  , Input
  , Popover
  , RadioButton
  , RadioGroup
  , Row
  , Submenu
  , Tooltip
  } from 'element-ui'
import '../theme/index.css'
import App from './App'
import router from './router'

Vue.config.productionTip = false

Vue.use(Aside)
Vue.use(Breadcrumb)
Vue.use(BreadcrumbItem)
Vue.use(Button)
Vue.use(Checkbox)
Vue.use(Col)
Vue.use(Container)
Vue.use(Form)
Vue.use(FormItem)
Vue.use(Header)
Vue.use(Main)
Vue.use(Menu)
Vue.use(MenuItem)
Vue.use(MenuItemGroup)
Vue.use(Input)
Vue.use(Popover)
Vue.use(RadioButton)
Vue.use(RadioGroup)
Vue.use(Row)
Vue.use(Submenu)
Vue.use(Tooltip)

Vue.component('user-tuple', {
  props: ['user'],
  template: '<li><tr><td>{{user.id}}</td><td>{{user.name}}</td><td>{{user.password}}</td></tr></li>'
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  // render: h => h(App),
  router,
  components: { App },
  template: '<App/>',
})



