<template>
  <el-container>
    <el-header class="header">
      <el-row type="flex">
        <el-col :span="1">
          <img class="header-img" src="../assets/logo.png" />
        </el-col>
        <el-col :span="8">
          <h1 class="header-title">应届生培养系统</h1>
        </el-col>
      </el-row>
    </el-header>
    <el-container>
      <el-aside class="asider">
        <el-menu
          default-active="2-1"
          class="navigator-menu"
          :collapse="isCollapse"
          background-color="#545c64"
          text-color="#ffffff"
          @select="handleSelect">
          <el-menu-item index="1">
            <template slot="title">
              <i :class="collapseIcon"></i>
              <span slot="title">{{ isCollapse ? '展开' : '折叠' }}</span>
            </template>
          </el-menu-item>
          <el-submenu index="2">
            <template slot="title">
              <i class="el-icon-location"></i>
              <span slot="title">位置</span>
            </template>
            <el-menu-item-group>
              <span slot="title">Group 2</span>
              <el-menu-item index="2-1">2-1</el-menu-item>
              <el-menu-item index="2-2">2-2</el-menu-item>
              <el-menu-item index="2-3">2-3</el-menu-item>
            </el-menu-item-group>
          </el-submenu>
        </el-menu>
      </el-aside>
      <el-container class="main-container">
        <el-header class="breadcrum">
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item to="/">Home</el-breadcrumb-item>
            <el-breadcrumb-item>Blah</el-breadcrumb-item>
            <el-breadcrumb-item>blah</el-breadcrumb-item>
            <el-breadcrumb-item>blah</el-breadcrumb-item>
          </el-breadcrumb>
        </el-header>
        <el-main class="main-content">
        </el-main>
      </el-container>
    </el-container>
  </el-container>
</template>

<script>
import { checkLoginInfo } from '../js/cookie'

checkLoginInfo();

export default {
  name: 'Home',
  data () {
    return {
      isCollapse: false
    }
  },
  computed: {
    collapseIcon() {
      return this.isCollapse ? 'el-icon-plus' : 'el-icon-minus';
    }
  },
  methods: {
    handleSelect(key, keyPath) {
      console.log(key, keyPath); // FIXME: DEBUG
      if (key === '1') {
        this.isCollapse = !this.isCollapse;
      }
    }
  }
}
</script>

<style>
.navigator-menu:not(.el-menu--collapse) {
  width: 200px;
}
.breadcrum {
  background-color: #eeeeee;
  height: min-content !important;
}
.main-content {
  background-color: #ffffff;
}
.asider {
  width: min-content !important;
}
</style>
<style scoped>
.header * {
  height: inherit;
}
.header {
  margin-bottom: 5px;
}
.header-title {
  margin-top: 14px;
  margin-bottom: 0px;
}
</style>



