import axios from 'axios'

export function getLoginInfo() {
  document.cookie = 'username=joseph;token=0b37a4482937c930;has-recent-activity=1;expires=Thu, 01 Jan 1970 00:00:00 UTC;path=/'
  //document.cookie = 'username=joseph;token=0b37a4482937c930;has-recent-activity=1;path=/'

  var cookies = document.cookie;
  let loginInfo = {
    username: undefined,
    token: undefined,
    recent: undefined
  };
  if (cookies.length === 0) {
    return loginInfo;
  }
  cookies = decodeURIComponent(cookies);
  cookies = cookies.split(';');
  for (let i = 0; i < cookies.length; i++) {
    let kv = cookies[i].trim();
    kv = kv.split('=');
    let k = kv[0].trim();
    let v = kv[1].trim();
    if (k === 'username') {
      loginInfo.username = v;
    } else if (k === 'token') {
      loginInfo.token = v;
    } else if (k === 'has-recent-activity') {
      loginInfo.recent = 1;
    }
  }
  return loginInfo;
}

function isInvalidValue(s) {
  return !s || s === '' || s.length === 0;
}

export function setUserToken(username, token, expireDays) {
  let msOfDays = expireDays * 24 * 60 * 60 * 1000;
  let expire = new Date();
  expire.setTime(expire.getTime() + msOfDays);
  document.cookie = `username=${username};token=${token};has-recent-activity=1;expires=${expire.toUTCString()};path=/`;
}

export function deleteUserToken(username, token) {
  document.cookie = `username=${username};token=${token};has-recent-activity=1;expires=Thu, 01 Jan 1970 00:00:00 UTC;path=/`
}

export function checkLoginInfo() {
  let loginInfo = getLoginInfo();
  if (isInvalidValue(loginInfo.username) || isInvalidValue(loginInfo.token)) {
    //location.href = '/login';
  } else {
    axios.post('/api/auth', {
      username: loginInfo.username,
      token: loginInfo.token
    })
    .then(response => {
      console.log(response) // FIXME: DEBUG
      const res = response.data;
      if (res.status && res.status.toLowerCase() === 'ok') {
        setUserToken(res.username, res.token, 1);
        if (false) {
          location.href = '/';
        }
      } else {
        alert('Token 已过期');
        deleteUserToken(res.username, res.token);
      }
    })
    .catch((error) => {
      console.log(error);
    });
  }
}



