<template>
  <div id="login-page">
    <div class="header">
      <div class="container">
        <img class="center-logo" src="../assets/logo.png"><br>
      </div>
    </div>
    <h1>用户登录</h1>
    <div>
      <el-radio-group class="radio-group" v-model="character">
        <el-radio-button class="radio-button" label="学生"></el-radio-button>
        <el-radio-button class="radio-button" label="导师"></el-radio-button>
        <el-radio-button class="radio-button" label="上级"></el-radio-button>
        <el-radio-button class="radio-button" label="管理员"></el-radio-button>
      </el-radio-group>
    </div>
    <div class="login-form-div">
      <el-form
        class="login-form"
        ref="authForm"
        :model="auth"
        :label-position="labelPosition"
        :rules="rules"
        label-width="80px"
        size="medium">
        <el-form-item label="用户名" prop="username">
          <el-input v-model="auth.username" maxlength="20"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="auth.password" type="password" minlength="6" maxlength="20"></el-input>
        </el-form-item>
        <div class="flex-row">
        <el-form-item class="left-align">
          <el-checkbox v-model="rememberUsername" disabled>记住用户名</el-checkbox>
        </el-form-item>
        <a class="justify" href="javascript:void(0)">忘记密码？</a>
        </div>
        <el-form-item>
          <el-button class="submit-button" :disabled="banLogin" type="primary" @click="submitForm('authForm')">登录</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

// import { checkLoginInfo } from '../js/cookie'
// checkLoginInfo();

const checks = [false, false];
function banLogin() {
  return checks.every(v => v) ? undefined : 'disabled';
}

function checkUsername(value, callback) {
  if (!value || value === '' || value.length === 0) {
    checks[0] = false;
    return callback(new Error('用户名不能为空'));
  }
  checks[0] = true;
  return callback();
}

function checkPassword(value, callback) {
  if (!value || (typeof value === 'string' && value.length < 8)) {
    checks[1] = false;
    return callback(new Error('密码必须大于8位'));
  } else if (typeof value === 'string' && !value.match(/^[A-Za-z0-9_]+$/)) {
    checks[1] = false;
    return callback(new Error('密码只能包含数字、英文大小写字母和下划线'));
  }
  checks[1] = true;
  return callback();
}

/**
 * 要求服务器返回JSON中含有 status, username, token 和菜单项。
 */
function postAuthData(authData) {
  axios.post('/api/auth', authData)
    .then((response) => {
      console.log(response);
      const res = response.data;
      if (res.status && res.status.toLowerCase() === 'ok') {
        document.cookie = `username=${res.username};token=${res.token};path=/`;
      } else {
        alert("Authentication failed");
      }
    })
    .catch((error) => {
      console.log(error);
    });
}

export default {
  name: 'Login',
  data () {
    var usernameValidator = (rule, value, callback) => {
        return checkUsername(value, callback);
      }
      , passwordValidator = (rule, value, callback) => {
        return checkPassword(value, callback);
      }

    setInterval(() => {
      checkUsername(this.auth.username, () => {});
      checkPassword(this.auth.password, () => {});
      this.banLogin = banLogin()
    }, 500);

    return {
      labelPosition: 'right',
      character: '学生',
      rememberUsername: false,
      auth: {
        username: '',
        password: ''
      },
      banLogin: true,
      rules: {
        username: [
          { validator: usernameValidator, trigger: 'blur' }
        ],
        password: [
          { validator: passwordValidator, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        let result;
        if (!valid) {
          result = false;
        } else {
          alert('submit');
          location.href = '/';
          postAuthData({
            character: this.character,
            username: this.auth.username,
            password: this.auth.password
          });
        }
        this.auth.password = '';
        return result;
      });
    }
  }
}
</script>

<style>
#login-page {
  width: 100%;
  height: 100%;
}
.header {
  width: 100%;
  padding-top: 32px;
  padding-bottom: 24px;
}
.login-form-div {
  margin: auto;
  margin-top: 32px;
  width: 400px;
}
.login-form {
  margin-left: -40px;
  margin-right: 40px;
}
.submit-button {
  width: 150px;
  font-size: 1.2em;
  font-weight: 500;
}
.el-radio-button__inner {
  font-size: 17px;
}
.left-align {
  text-align: left;
}
.flex-row {
  display: flex;
  flex-direction: row;
}
a {
  text-decoration: none;
  color: #4488BB;
  font-size: 14px;
}
.justify {
  margin-left: 156px;
  margin-top: 6px;
}
</style>



